body{
    color: #1a202c;
 
    font-family: sans-serif;
    background: linear-gradient(
      90deg,
      rgb(255, 212, 128) 0%,
      rgb(245, 98, 98) 100%
    );

}
.main-body {
    padding: 15px;
}
.card {
    box-shadow: 0 1px 3px 0 rgba(0,0,0,.1), 0 1px 2px 0 rgba(0,0,0,.06);
}

.card {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-color: #fff;
    background-clip: border-box;
    border: 0 solid rgba(0,0,0,.125);
    border-radius: .25rem;
}

.card-body {
    flex: 1 1 auto;
    min-height: 1px;
    padding: 1rem;
}

.gutters-sm {
    margin-right: -8px;
    margin-left: -8px;
}

.gutters-sm>.col, .gutters-sm>[class*=col-] {
    padding-right: 8px;
    padding-left: 8px;
}
.mb-3, .my-3 {
    margin-bottom: 1rem!important;
}

.bg-gray-300 {
    background-color: #e2e8f0;
}
.h-100 {
    height: 100%!important;
}
.shadow-none {
    box-shadow: none!important;
}

.box {
    margin: 0;
    border: 0;
}


  

.itemF {grid-area: filter;}
.itemL {grid-area: auto;}


.grid-container {
  display: grid;
  grid-template-areas:
    'filter auto auto'
    'filter auto auto'
    'filter auto auto';
  gap: 10px;
  padding: 10px;
}

.grid-task {
  text-align: center;
}
  

